import {Component, ChangeDetectionStrategy, ChangeDetectorRef} from '@angular/core';
import {FormBuilder} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {AuthGuard, Authorize} from '@anglr/authentication';
import {ComponentRedirectRoute, ComponentRoute} from '@anglr/common/router';
import {AsyncDataLoaderOptions, SimpleOrdering} from '@anglr/grid';
import {Observable} from 'rxjs';

import {BasePrehladComponent} from '../../../misc/basePrehladComponent';
import {ZariadeniaService, ZariadenieItem} from '../../../services/api/zariadenia';
import {Orderable, Pageable, PagedData} from '../../../misc/types';

/**
 * Page used for displaying preview of 'zariadenie'
 */
@Component(
{
    selector: 'prehlad-view',
    templateUrl: 'prehlad.component.html',
    styleUrls: ['prehlad.component.scss'],
    providers: [ZariadeniaService],
    changeDetection: ChangeDetectionStrategy.OnPush
})
@Authorize('zariadenia-prehlad-page')
@ComponentRedirectRoute('', 'prehlad', true)
@ComponentRoute({path: 'prehlad', canActivate: [AuthGuard]})
export class ZariadeniaPrehladComponent extends BasePrehladComponent<any, ZariadenieItem>
{
    //######################### protected properties #########################

    /**
     * Gets default value of filter
     */
    protected get defaultFilterValue(): any
    {
        return {
            btsId : '', 
            devId : '',  
            devId1 : '', 
            deviceId : '',   
            name : '',
            sim  : '',
            type : ''
        };
    }

    //######################### constructor #########################
    constructor(formBuilder: FormBuilder,
                route: ActivatedRoute,
                router: Router,
                private _dataSvc: ZariadeniaService,
                changeDetector: ChangeDetectorRef)
    {
        super(formBuilder, route, router, changeDetector);

        this.gridOptions =
        {
            plugins:
            {
                dataLoader:
                {
                    options: <AsyncDataLoaderOptions<ZariadenieItem, SimpleOrdering>>
                    {
                        dataCallback: this._getData
                    }
                }
            }
        };
    }

    //######################### protected methods #########################
    
    /**
     * Gets data for prehlad
     * @param paging Information about requested paging
     * @param ordering Information about requested ordering
     * @param filter Information about requested filter
     */
    protected getPrehlad(paging?: Pageable,
                         ordering?: Orderable,
                         filter?: any): Observable<PagedData<ZariadenieItem>>
    {
        return this._dataSvc.getAllZariadenia(filter, paging, ordering);
    }
}