import {Pipe, PipeTransform} from '@angular/core';
import {FormControl, FormGroup} from '@angular/forms';

/**
 * Pipe that is used to visualize form group value
 */
@Pipe({name: 'formValuePreview'})
export class FormValuePreviewPipe implements PipeTransform
{
    /**
     * Transforms form group value to string
     * @param group Form group to be visualized
     */
    public transform(group: FormGroup): string
    {
        const result = [];
        
        for(const controlName in group.controls)
        {
            const control = group.get(controlName);

            if(control instanceof FormControl && !!control.value)
            {
                result.push(`${controlName}: ${control.value}`);
            }
        }

        return result.join(' | ');
    }
}