import {OnInit, ViewChild, Directive, ChangeDetectorRef} from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {GridOptions, SimpleOrdering, DataResponse, GridComponent} from '@anglr/grid';
import {setPage, refreshData} from '@anglr/grid/extensions';
import {prepareForFormBuilder} from '@anglr/common/forms';
import {readEncodedFilterWithDates} from '@anglr/common/date-fns';
import {serializeToUrlQuery} from '@jscrpt/common';
import {Observable} from 'rxjs';

import {Orderable, Pageable, PagedData} from './types';
import {TransformOrdering} from './decorators';

/**
 * Base class for all "prehlad" pages
 */
@Directive()
export abstract class BasePrehladComponent<TFilter, TDataItem> implements OnInit
{
    //######################### protected page #########################

    /**
     * Current paging used for loading data
     */
    protected _currentPaging: Pageable;

    //######################### public properties - template bindings #########################

    /**
     * Options for grid
     */
    public gridOptions: GridOptions;


    /**
     * Filter form group
     */
    public filter: FormGroup;

    /**
     * Indication whether is filter collapsed
     */
    public filterCollapsed: boolean = false;

    //######################### protected properties #########################

    /**
     * Gets default value of filter
     */
    protected abstract get defaultFilterValue(): TFilter;

    //######################### public properties - children #########################

    /**
     * Instance of grid component
     */
    @ViewChild('grid', {static: false})
    public grid: GridComponent;

    //######################### constructors #########################
    constructor(protected _formBuilder: FormBuilder,
                protected _route: ActivatedRoute,
                protected _router: Router,
                protected _changeDetector: ChangeDetectorRef)
    {
    }

    //######################### public methods - implementation of OnInit #########################
    
    /**
     * Initialize component
     */
    public ngOnInit()
    {
        let filterDefault: TFilter = this.defaultFilterValue;

        if(this._route.snapshot.paramMap.has('filter'))
        {
            filterDefault = readEncodedFilterWithDates(filterDefault, this._route.snapshot.paramMap.get('filter'));
        }

        this.filter = this._formBuilder.group(prepareForFormBuilder(this.defaultFilterValue));
        this.filter.patchValue(filterDefault, {emitEvent: false});
    }

    //######################### public methods #########################

    /**
     * Refresh to defaults grid content
     */
    public refreshGridToDefaults()
    {
        this.grid.execute(setPage(1),
                          refreshData(true));
    }

    /**
     * Refresh grid data
     */
    public refreshGrid()
    {
        this.grid.execute(refreshData(true));
    }

    /**
     * Resets filter to its default value
     */
    public async resetFilter()
    {
        this.filter.patchValue(this.defaultFilterValue);

        await this._router.navigate(['.', {}],
                                    {
                                        relativeTo: this._route,
                                        queryParamsHandling: 'merge',
                                        replaceUrl: true
                                    });

        this.refreshGridToDefaults();
    }

    /**
     * Runs search
     */
    public async search()
    {
        await this._router.navigate(['.', {filter: serializeToUrlQuery(this.filter.value)}],
                                    {
                                        relativeTo: this._route,
                                        queryParamsHandling: 'merge',
                                        replaceUrl: true
                                    });

        this.refreshGridToDefaults();
    }

    /**
     * Explicitly runs invalidation of content (change detection)
     */
    public invalidateVisuals(): void
    {
        this._changeDetector.detectChanges();
    }

    //######################### protected methods #########################

    /**
     * Callback used for obtaining data
     * @param page Index of requested page
     * @param itemsPerPage Number of items per page
     * @param ordering Order by column name
     */
    @TransformOrdering
    protected async _getData(page: number, itemsPerPage: number, ordering: SimpleOrdering&Orderable): Promise<DataResponse<TDataItem>>
    {
        this._currentPaging = 
        {
            page: page - 1,
            size: itemsPerPage
        };

        const result = await this.getPrehlad(this._currentPaging,
                                             ordering,
                                             this._getFilterValues()).toPromise();

        return {
            data: result.content,
            totalCount: result.totalElements
        };
    }

    /**
     * Gets data for prehlad
     * @param paging Information about requested paging
     * @param ordering Information about requested ordering
     * @param filter Information about requested filter
     */
    protected abstract getPrehlad(paging?: Pageable,
                                  ordering?: Orderable,
                                  filter?: TFilter): Observable<PagedData<TDataItem>>;

    /**
     * Gets filter value
     */
    protected _getFilterValues(): TFilter
    {
        return this.filter.value;
    }
}