import {SimpleOrdering} from '@anglr/grid';
import {isFunction, isPresent, OrderByDirection} from '@jscrpt/common';

import {Orderable} from '../types';

/**
 * Transforms SimpleOrdering into Orderable for REST
 */
// eslint-disable-next-line @typescript-eslint/ban-types
export function TransformOrdering(_target: Object, propertyKey: string, descriptor: PropertyDescriptor)
{
    // eslint-disable-next-line @typescript-eslint/ban-types
    const originalValue: Function = descriptor.value ?? descriptor.get();

    if(!isFunction(originalValue))
    {
        throw new Error(`Unable to apply @TransformOrdering decorator to '${propertyKey}', it is not a method.`);
    }

    return {
        configurable: true,
        get(this: any): any
        {
            const bound: any = originalValue.bind(this);

            const func = function(page: number, itemsPerPage: number, ordering: SimpleOrdering)
            {
                let reqOrdering: Orderable = null;

                if(ordering)
                {
                    reqOrdering =
                    {
                        direction: ordering.orderByDirection,
                        sort: ordering.orderBy
                    };
                }

                if(reqOrdering)
                {
                    if(reqOrdering.sort && isPresent(reqOrdering.direction))
                    {
                        reqOrdering.sort = `${reqOrdering.sort},${reqOrdering.direction == OrderByDirection.Descendant ? 'desc' : 'asc'}`;
                    }
                    else
                    {
                        delete reqOrdering.sort;
                    }

                    delete reqOrdering.direction;
                }

                return bound(page, itemsPerPage, reqOrdering);
            };

            const funcBound = func.bind(this);

            Object.defineProperty(this,
                                  propertyKey,
                                  {
                                      value: funcBound,
                                      configurable: true,
                                      writable: true
                                  });

            return funcBound;
        }
    };
}